<template>
  <div id="app">
    <div class="container-fluid">
      <div class="shadow">
        <div class="header">T KIM TRA Y T VI COVID-19</div>
        <div class="counter" v-if="this.current_question<6">
          <div class="mark align-middle inline"></div>
          <div class="text align-middle inline">Hy tr li cu hi sau</div>
        </div>
        <div class="counter" v-else>
          <div class="mark align-middle inline"></div>
          <div class="text align-middle inline">Kt lun</div>
        </div>
        <!-- Question 01-->
        <div class="content_area" v-bind:style="checkdisplay(1)" id="q1">
          <div class="question">{{ q1_data.q }}</div>
          <div class="radiobox" v-for="(af,index) in q1_data.a" v-bind:key="index">
            <input
              type="radio"
              aria-label="a"
              name="a1"
              v-bind:id="index"
              v-bind:value="index"
              v-model="current_answer"
            />
            <label v-bind:for="index">{{af}}</label>
          </div>
        </div>

        <!-- Question 04-->
        <div class="content_area" v-bind:style="checkdisplay(2)" id="q2">
          <div class="question">Bn ang  tnh thnh no?</div>
          <div class="radiobox">
            <input type="radio" aria-label="a" name="a2" id="a20" value="0" v-model="danger_city" />
            <label for="a20">Cc tnh thnh cha c ca nhim</label>
          </div>
          <div class="radiobox">
            <input type="radio" aria-label="a" name="a2" id="a21" value="1" v-model="danger_city" />
            <label for="a21">Cc tnh thnh  c ca nhim: H Ni, Vnh Phc, H Ch Minh, ...</label>
          </div>
        </div>

        <!-- Question 04-->
        <div class="content_area" v-bind:style="checkdisplay(3)" id="q3">
          <div class="question">Bn c ang gp nhng triu chng no sau y khng?</div>
          <div class="radiobox">
            <input type="radio" aria-label="a" name="a3" id="a30" value="0" v-model="ill" />
            <label for="a30">Khng gp bt k triu chng no i y</label>
          </div>
          <div class="radiobox">
            <input type="radio" aria-label="a" name="a3" id="a31" value="1" v-model="ill" />
            <label for="a31">Ho, st, kh th v au hng</label>
          </div>
        </div>

        <!-- Question 04-->
        <div class="content_area" v-bind:style="checkdisplay(4)" id="q4">
          <div class="question">Bn c ang  din cch ly cng ng khng?</div>
          <div class="radiobox">
            <input
              type="radio"
              aria-label="a"
              name="a4"
              id="a40"
              value="0"
              v-model="isolation_status"
            />
            <label for="a40">C</label>
          </div>
          <div class="radiobox">
            <input
              type="radio"
              aria-label="a"
              name="a4"
              id="a41"
              value="1"
              v-model="isolation_status"
            />
            <label for="a41">Khng</label>
          </div>
        </div>

        <!-- Question 04-->
        <div class="content_area" v-bind:style="checkdisplay(5)" id="q5">
          <div class="question">Trong vng 14 ngy bn c tip xc no nh di y khng?</div>
          <div class="radiobox">
            <input
              type="radio"
              aria-label="a"
              name="a5"
              id="a50"
              value="0"
              v-model="relation_status"
            />
            <label for="a50">Tip xc vi bnh nhn nhim COVID-19</label>
          </div>
          <div class="radiobox">
            <input
              type="radio"
              aria-label="a"
              name="a5"
              id="a51"
              value="1"
              v-model="relation_status"
            />
            <label for="a51">Tip xc vi ngi nghi nhim COVID-19</label>
          </div>
          <div class="radiobox">
            <input
              type="radio"
              aria-label="a"
              name="a5"
              id="a52"
              value="2"
              v-model="relation_status"
            />
            <label for="a52">Tip xc vi ngi tip xc vi ngi nghi nhim COVID-19</label>
          </div>
          <div class="radiobox">
            <input
              type="radio"
              aria-label="a"
              name="a5"
              id="a53"
              value="3"
              v-model="relation_status"
            />
            <label for="a53">Khng tip xc i tng no lin quan n COVID-19</label>
          </div>
        </div>
        <!-- Mess 04-->
        <div class="content_area" v-bind:style="checkdisplay(6)" id="q6">
          <div class="icon-wrapper">
            <img class="icon" src="~/./assets/icon.jpg" />
          </div>
          <div class="last_message">{{this.current_message}}</div>
        </div>

        <div class="control" v-if="this.current_question<6">
          <div class="col-xs-6 inline">
            <button class="cancelbtn" v-on:click="handleCancel">Hy b</button>
          </div>
          <div class="col-xs-6 inline">
            <button class="nextbtn" v-on:click="handleNext">TIP THEO</button>
          </div>
        </div>
        <div class="control_last" v-else>
          <div class="col-xs-12">
            <button class="nextbtn" v-on:click="handleReset">Tr li li</button>
          </div>
        </div>
      </div>
      <div class="footer">
        Thng tin trc nghim ch mang tnh cht tham kho, hy lin h cc c quan Y t  nhn thng tin t vn cui cng.
        <br />Mt sn phm ca M Ai Blog (https://ainoodle.vn). Ni dung trc nghim tham kho ca Bo Thanh nin.
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      publicPath: process.env.BASE_URL,
      previous_question: -1,
      isolation_status: -1,
      relation_status: -1,
      danger_city: -1,
      ill: -1,
      current_message: "",
      current_question: 1,
      current_answer: -1,
      q1_data: {
        q: "Bn c va tr v t nc ngoi khng?",
        a: [
          "Khng ti ch  Vit Nam",
          "Ti tr v t Trung Quc",
          "Ti tr v t Hn Quc",
          "Ti tr v t Chu u, M",
          "Tr v t cc nc ASEAN",
          "Tr v t cc quc gia khc"
        ]
      },
      message_list: [
        "Bn tr v t nc ngoi. Do  c triu chng nn bn cn c cch ly iu tr ti c s y t",
        "Bn tr v t nc ngoi. D khng c cc triu chng nhng bn vn cn c cch ly tp trung, theo di sc kho trong vng 14 ngy",
        "Bn tr v t nc ngoi. Thc hin khai bo y t. Cch ly, gim st y t ti gia nh, doanh nghip, c s lu tr, gim st theo nhm",
        "Bn ch  Vit Nam v  tnh/thnh khng c dch. Tuy nhin bn c triu chng ho st nn bn hy bnh tnh, eo khu trang v hn ch tip xc vi mi ngi. Gi cho ng dy nng 19003228 hoc 19009095  c t vn, khm v iu tr. ",
        "Bn ch  Vit Nam v  tnh/thnh khng c dch. Hin cha c triu chng g, bn hy phng bnh theo khuyn co ca B Y t. ",
        "Bn  tnh/thnh c dch v ang  khu vc cch ly cng ng. Tun th ng cc quy nh v cch ly ca c quan chc nng.",
        "Bn  tnh/thnh c dch. Do  tip xc gn vi bnh nhn v c triu chng nn bn cn c cch ly iu tr ti c s y t ",
        "Bn  tnh/thnh c dch. Do  tip xc gn vi ngi nghi nhim nn bn cn bn cn c cch ly tp trung, theo di sc khe",
        "Bn  tnh/thnh c dch. Do  tip xc gn vi ngi tip xc vi ngi nghi nhim nn bn cn c cch ly ti nh. Hy tun th cc quy nh v cch ly ca c quan chc nng",
        "Bn  tnh/thnh c dch. Hin cha c triu chng g, bn hy phng bnh theo khuyn co ca B Y t.",
        "Bn  tnh/thnh c dch. Bn khng tip xc vi cc ngun lin quan nhng bn c triu chng ho st nn bn hy bnh tnh, eo khu trang v hn ch tip xc vi mi ngi. Gi cho ng dy nng 19003228 hoc 19009095  c t vn, khm v iu tr. "
      ]
    };
  },
  methods: {
    checkdisplay: function(index) {
      if (index === this.current_question) {
        //console.log("Bang")
        return { display: "block" };
      } else {
        //console.log("Khac")
        return { display: "none" };
      }
    },
    handleCancel: function() {
      this.current_message = "Bn  hy trc nghim";
      this.showmessage();
    },

    handleReset: function() {
      this.isolation_status = -1;
      this.relation_status = -1;
      this.danger_city = -1;
      this.ill = -1;
      this.current_message = "";
      this.current_question = 1;
      this.current_answer = -1;
    },

    showmessage() {
      //alert(this.current_message);
      this.current_question = 6;
    },

    handleNext: function() {
      if (this.current_question <= 5)
        if (this.current_question == 1) {
          // Hi v nc ngoi v -----------------------------------------
          // Check cau hoi
          if (this.current_answer == -1) {
            alert("Hy chn mt cu tr li");
            return;
          }
          //console.log(this.current_answer);
          if (1 <= this.current_answer && this.current_answer <= 4) {
            this.previous_question = 1;
            this.current_question = 3;
          } else if (this.current_answer == 5) {
            // Cc nc an ton
            this.current_message = this.message_list[2];
            this.showmessage();
          } else {
            // Khng i nc ngoi v
            this.current_question = 2;
            return;
          }
          // Thnh ph nhim bnh -------------------------------
        } else if (this.current_question == 2) {
          if (this.danger_city == -1) {
            alert("Hy chn mt cu tr li");
            return;
          }
          if (this.danger_city == 0) this.current_question++;
          else {
            this.current_question = 4;
          }

          // Tinh trang suc khoe --------------------------------
        } else if (this.current_question == 3) {
          if (this.ill == -1) {
            alert("Hy chn mt cu tr li");
            return;
          }
          // Neu chuyen tu form 1 sang (khai bao nhap canh)

          if (this.previous_question == 1) {
            if (this.ill == 1) {
              // m
              this.current_message = this.message_list[0];
              this.showmessage();
            } else {
              // Khng m
              this.current_message = this.message_list[1];
              this.showmessage();
            }
            return;
          }
          // Chuyen tu form tip xc
          if (this.previous_question == 5) {
            if (this.ill == 1) {
              // m
              this.current_message = this.message_list[10];
              this.showmessage();
            } else {
              // Khng m
              this.current_message = this.message_list[9];
              this.showmessage();
            }
            return;
          }

          // Neu o tnh thnh an ton
          if (this.danger_city == 0) {
            if (this.ill == 1) this.current_message = this.message_list[3];
            else this.current_message = this.message_list[4];

            this.showmessage();
            // Nu  tnh thnh khng an ton
          } else {
            if (this.ill == 1) {
              this.current_message = this.message_list[6];
              this.showmessage();
            } else {
              this.current_message = this.message_list[7];
              this.showmessage();
            }
          }
          // C b cch ly ko ------------------------------
        } else if (this.current_question == 4) {
          if (this.isolation_status == -1) {
            alert("Hy chn mt cu tr li");
            return;
          }
          // C cch ly
          if (this.isolation_status == 0) {
            this.current_message = this.message_list[5];
            this.showmessage();
            return;
          }
          // Khng cch ly, chuyn cu tip xc
          else {
            this.current_question = this.current_question + 1;
          }
          // C tip xc ko ---------------------
        } else if (this.current_question == 5) {
          if (this.relation_status == -1) {
            alert("Hy chn mt cu tr li");
            return;
          }
          console.log(this.relation_status);
          // Nu tip xc bnh nhn
          if (this.relation_status == 0) {
            // Hi sc khe
            this.current_question = 3;
            // Nu tip xc nghi ng
          } else if (this.relation_status < 3) {
            // Khuyn co
            console.log(7 + this.relation_status);
            this.current_message = this.message_list[
              7 + parseInt(this.relation_status)
            ];
            this.showmessage();
            // Nu khong lm g
          } else {
            this.previous_question = 5;
            this.current_question = 3;
          }
        }
    }
  }
};
</script>

<style>
body {
  background-color: rgb(235, 235, 235);
  margin-bottom: 20px;
  margin-top: 20px;
}
.icon {
  max-width: 100%;
}
.icon-wrapper {
  text-align: center;
}

.content_area {
  background-color: white;
  padding-bottom: 20px;
}

.header {
  border-radius: 10px 10px 0px 0px;
  background-color: seagreen;
  color: white;
  padding: 20px;
  margin-top: 10px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: large;
}

.counter {
  background-color: white;
  color: black;
  padding: 10px;
}

.question {
  color: black;
  padding: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.control {
  color: black;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.last_message {
  color: black;
  padding: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  text-align: center;
}

.control_last {
  color: black;
  padding: 10px;
  text-align: center;
}

.inline {
  display: inline-block;
}

.mark {
  background-color: red;
  width: 5px;
  height: 30px;
}

.text {
  text-align: center;
}

.radiobox {
  padding: 0px 10px;
}

.cancelbtn {
  text-transform: uppercase;
  background-color: lightgray;
  color: gray;
  border: 0px;
  padding: 8px 20px;
  border-radius: 8px;
}

.nextbtn {
  text-transform: uppercase;
  background-color: seagreen;
  color: white;
  border: 0px;
  padding: 8px 20px;
  border-radius: 8px;
}

.shadow {
  box-shadow: 5px 10px #888888;
  border-radius: 10px;
}

.combobox {
  margin-left: 10px;
}

.label {
  margin-top: 10px;
  margin-left: 10px;
}

.footer {
  padding: 10px;
  font-size: x-small;
  text-align: center;
  color: #888888;
}
</style>


